<template>
    <div>
        <el-button type="primary" icon="el-icon-plus" @click="formVisible=true">
            添加项目
        </el-button>
        <!--弹出窗口-->
        <el-dialog  title="添加项目" :visible.sync="formVisible" >
                    <el-form label-width="80px">
            <el-form :model="prod" ref="prodForm">
        <el-form-item label="项目名称"><el-input v-model="prod.project"></el-input></el-form-item>
        <el-form-item label="项目金额"><el-input v-model="prod.money"></el-input></el-form-item>
        <el-form-item label="完成时间">
                <el-date-picker
                    v-model="prod.time"
                    size="mini"
                    type="date"
                    value-format="yyyy-MM-dd"
                    style="width: 150px;"
                    placeholder="完成日期">
                </el-date-picker>
        </el-form-item>
        <el-form-item label="团队人数"><el-input v-model="prod.people"></el-input></el-form-item>
        <el-form-item label="所属部门"><el-input v-model="prod.depa"></el-input></el-form-item>
        </el-form>
            <el-form-item>
                <el-button type="primary" @click="save()">保存</el-button>
                <el-button @click="formVisible = false" >关闭</el-button>
            </el-form-item>
                    </el-form>
                </el-dialog>

        <el-table
    :data="tableDate"
    stripeProj
    style="width: 100%">
    <el-table-column
      prop="project"
      label="项目名称"
      width="180">
    </el-table-column>
    <el-table-column
      prop="money"
      label="金额"
      width="180">
    </el-table-column>
    <el-table-column
      prop="time"
      label="时间"
      width="180"
      >
    </el-table-column>
    <el-table-column
      prop="depa"
      label="部门团队"
      width="180"
      >
    </el-table-column>
    <el-table-column
      prop="people"
      label="团队人数"
      width="180"
      >
    </el-table-column>
    <el-table-column label="操作" >
        <template slot-scope="scope">
            <el-button @click="showEditView(scope.row)">编辑</el-button>
            <el-button type="danger" @click="deleteOne(scope.row)">删除</el-button>
        </template>
    </el-table-column>
  </el-table>
    </div>
</template>

<script>
    export default {
        name: "StaAll",
        data(){
            return {
                tableDate:[],
                formVisible:false,
                prod:{},
            }
        },
       mounted() {
           this.getProductionList();
       },
        methods: {
            getProductionList(){
                this.getRequest("/statistics/all/").then(resp =>{
                    if(resp){
                        this.tableDate = resp;
                    }
                })
            },
             deleteOne(data) {
                this.$confirm('此操作将删除【' + data.project + '】项目，是否继续？', '提示', {
                    cancelButtonText: '取消',
                    confirmButtonText: '确定'
                }).then(() => {
                    this.deleteRequest("/statistics/all/" + data.id).then(resp => {
                        if (resp) {
                            this.getProductionList();
                        }
                    })
                }).catch(() => {
                    this.$message.info("取消删除!");
                })
            },
            emptyProd(){
                this.prod ={
                    project:"",
                    money:"",
                    time:"",
                    people:"",
                    depa:""

                }

            },
            save(){
                if(this.prod.id){
                    this.$refs['prodForm'].validate(valid => {
                            if (valid) {
                                this.postRequest("/statistics/all/update/", this.prod).then(resp => {
                                    if (resp) {
                                        this.formVisible = false;
                                        this.getProductionList();
                                    }
                                })
                            }
                        });
                    }else{
                        this.$refs['prodForm'].validate(valid => {
                            if (valid) {
                                this.postRequest("/statistics/all/add/", this.prod).then(resp => {
                                    if (resp) {
                                        this.formVisible = false;
                                        this.getProductionList();
                                    }
                                })
                            }
                        });
                }

            },
            showEditView(data){
                this.getProductionList();
                this.title = '编辑项目信息';
                this.formVisible = true;
                this.prod= data;
            }   
        }
    }
</script>

<style scoped>

</style>