<template>
    <div id="StaScore">
        
        <div id="main" style="width: 600px;height:400px;"></div>
        
    </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      
    }
  },
   mounted() {
    this.getReceList();
   
  },
  methods: {
    
    getReceList(){
        this.getRequest("/statistics/all/").then(resp=>{
            if(resp){
              this.receList = resp;
            }
            //创建饼图


    let legendData = []; //图例
    let receData = [];
    for(let i=0 ;i< this.receList.length;i++){
        legendData.push(this.receList[i].depa);
        receData.push(this.receList[i].money )
    }
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("main"));
      // 指定图表的配置项和数据
      let option = {
        title: {
          text: "各部门营业额统计"
        },
        tooltip: {},
        legend: {
          data: ["收益"]
        },
        xAxis: {
          data: legendData
        },
        yAxis: {},
        series: [
          {
            name: "收益",
            type: "bar",
            data: receData
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
    })
    },
  }
}
</script>

