<template>
    <div id="app">
       
    <div id="left">
      <el-button type="primary" icon="el-icon-plus" @click="formVisible=true">
            添加项目
        </el-button>
        <!-- 弹出列表 -->
<el-dialog  title="添加" :visible.sync="formVisible" >
                    <el-form label-width="80px">
            <el-form :model="cost" ref="costForm">
        <el-form-item label="支出类型"><el-input v-model="cost.type"></el-input></el-form-item>
        <el-form-item label="支出金额"><el-input v-model="cost.money"></el-input></el-form-item>
        <el-form-item label="操作时间">
                <el-date-picker
                    v-model="cost.time"
                    size="mini"
                    type="date"
                    value-format="yyyy-MM-dd"
                    style="width: 150px;"
                    placeholder="完成日期">
                </el-date-picker>
        </el-form-item>
        <el-form-item label="处理人"><el-input v-model="cost.people"></el-input></el-form-item>
        <el-form-item label="备注"><el-input v-model="cost.beizhu"></el-input></el-form-item>
        </el-form>
            <el-form-item>
                <el-button type="primary" @click="save()">保存</el-button>
                <el-button @click="formVisible = false" >关闭</el-button>
            </el-form-item>
                    </el-form>
                </el-dialog>

      <!-- 表单 -->
            <el-table
        :data="costList"
        stripe
        style="width: 100%">
        <el-table-column
          prop="type"
          label="支出类型"
          width="120">
        </el-table-column>
        <el-table-column
          prop="money"
          label="金额"
          width="120">
        </el-table-column>
        <el-table-column
          prop="time"
          label="时间"
          width="120"
          >
        </el-table-column>
        <el-table-column label="操作" width="150px">
            <template slot-scope="scope">
                <el-button @click="showEditView(scope.row)">编辑</el-button>
                <el-button type="danger" @click="deleteOne(scope.row)">删除</el-button>
            </template>
        </el-table-column>
      </el-table>
  </div>
  
  <div id="right"><div id="main" style="width: 600px;height:400px;"></div></div>
    </div>
</template>

<script>
export default {
  name: "app",
  data() {
      return {
          costList:[],
          formVisible:false,
          cost:{},
      }
  },
   mounted() {
    this.getCostList();
  },
  methods: {
     deleteOne(data) {
        this.$confirm('此操作将删除【' + data.type + '】项目，是否继续？', '提示', {
            cancelButtonText: '取消',
            confirmButtonText: '确定'
        }).then(() => {
            this.deleteRequest("/statistics/personnel/" + data.id).then(resp => {
                if (resp) {
                    this.getCostList();
                }
            })
        }).catch(() => {
            this.$message.info("取消删除!");
        })
    },
      emptyCost(){
          this.cost ={
              type:"",
              money:"",
              time:"",
              people:"",
              beizhu:""
          }
      },
      
      showEditView(data){
          this.getCostList();
          this.title = '编辑信息';
          this.formVisible = true;
          this.cost= data;
      },
      save(){
        if(this.cost.id){
            this.$refs['costForm'].validate(valid => {
                    if (valid) {
                        this.postRequest("/statistics/personnel/update/", this.cost).then(resp => {
                            if (resp) {
                                this.formVisible = false;
                                this.getCostList();
                            }
                        })
                    }
                });
            }else{
                this.$refs['costForm'].validate(valid => {
                    if (valid) {
                        this.postRequest("/statistics/personnel/add/", this.cost).then(resp => {
                            if (resp) {
                                this.formVisible = false;
                                this.getCostList();
                            }
                        })
                    }
                });
        }

    },
      getCostList(){
        this.getRequest("/statistics/personnel/").then(resp=>{
            if(resp){
              this.costList = resp;
            }
            //创建饼图

    let legendData = []; //图例
    let costData = [];
    for(let i=0 ;i< this.costList.length;i++){
        legendData.push(this.costList[i].type);
        costData.push({name: this.costList[i].type , value:this.costList[i].money })
    }
    
    // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("main"));
      // 指定图表的配置项和数据
      let option = {
    tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)'
    },
    legend: {
        orient: 'vertical',
        right: 10,
        data: legendData
    },
    series: [
        {
            name: '支出明细',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
                show: false,
                position: 'center'
            },
            emphasis: {
                label: {
                    show: true,
                    fontSize: '30',
                    fontWeight: 'bold'
                }
            },
            labelLine: {
                show: false
            },
            data: costData
        }
    ]
};

      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
        })
        
    }, 
  }
}
</script>

<style scoped>

#left {
float: left;
}
#right {
float: right;

}

</style>