<template>
    <div id="app">
        
        <div id="main" style="width: 600px;height:400px;"></div>
        
    </div>
</template>

<script>
export default {
  name: "app",
  mounted() {
    this.receCount();
  },
  methods: {
    receCount() {

      this.getRequest("/statistics/recored/").then(resp=>{
            if(resp){
              this.costList = resp;
            }

      let legendData = []; //图例
      let costData = [];
    for(let i=0 ;i< this.costList.length;i++){
        legendData.push(this.costList[i].time);
        costData.push(this.costList[i].money)
    }
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("main"));
      // 指定图表的配置项和数据
      let option = {
    xAxis: {
        type: 'category',
        data: legendData
    },
    yAxis: {
        type: 'value'
    },
    series: [{
        data: costData,
        type: 'line'
    }]
};

      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
      })
    },
  }
 
};
</script>

